import React, { Component } from 'react';
import { Link } from 'react-router';
import ArticleContent from '../components/blog/ArticleContent';
// import ArticleHot from './ArticleHot';
// import ArticleComment from './ArticleComment';

import blog from '../actions/blog';
import { Status } from '../constants';

class ArticlePanel extends Component {
  constructor(props) {
    super(props);
    props.getBlogDetail(props.blogId);
  }

  componentWillReceiveProps(nextProps) {
    if ( nextProps.blogId != this.props.blogId ) {
      nextProps.getBlogDetail(nextProps.blogId);
    }
  }

  render() {
    return (
      <div className="article-main">
        <ArticleContent 
          blogId={this.props.blog.blogId || -1}
          title={this.props.blog.title || '此文章不存在'}
          createAt={this.props.blog.time.createAt || '0000:00:00'}
          updateAt={this.props.blog.time.updateAt || '0000:00:00'}
          category={this.props.blog.category || ''}
          content={this.props.blog.content || ''}
          tags={this.props.blog.tags || ''}
        />
        {/*
          this.state.blog.blogId != -1 ? 
          <ArticleHot
            blogId={this.state.blog.blogId || -1}
            numbers={this.state.blog.numbers}
          />
          : ''
        */}
        {/*
          this.state.blog.blogId != -1 ? 
          <ArticleComment
            blogId={this.state.blog.blogId || -1}
          />
          : ''
        */}
      </div>
    )
  }

};

const mapStateToProps = (state) => {
  const branch_state = state.blog.currentBlog;
  return branch_state;
} 

const mapDispatchToProps = (dispatch) => ({ 
  getBlogDetail: (blogId) => dispatch(blog.getBlogDetail(blogId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ArticlePanel);


